import React, {Component} from 'react'
import '../styles/padStyles.css'
import '../index.css'

let sound;

class NewPad extends Component {
    state = {
        padActive: false
    }

    activatePad() {
        this.setState({padActive: true})
    }

    playSound = () => {
        /* When clicked with mouse */
        sound = new Audio()
        sound.src = this.props.audio

        if(this.props.muted === false){ // if not muted
            sound.pause()
            sound.currentTime = 0;
            sound.play()


            if(this.props.trigger === 'trigger'){
                sound.play()
            }else if(this.props.trigger === 'nonTrigger'){
                sound.pause()
            }

            // for on click style
            setTimeout(() => this.activatePad(), 80);
            this.setState({padActive: false})
        }
        /* When clicked with mouse */

        // Display Text Change
        document.querySelector("h5").innerText = `${this.props.onScreenText}`
    }

    componentDidMount() {
        document.addEventListener("keydown", event => {
            if(event.key.toUpperCase() === this.props.id){

                sound = new Audio()
                sound.src = this.props.audio
                
                if(this.props.muted === false){ // if not muted
                    sound.play()

                    // for on click style
                    setTimeout(() => this.activatePad(), 80);
                    this.setState({padActive: false})

                    // console.log(this.state.padActive)
                    
                   
                }


                if(this.props.trigger === 'trigger'){
                    sound.play()
                }else if(this.props.trigger === 'nonTrigger'){
                    sound.pause()
                }
                
                // Display Text Change
                document.querySelector("h5").innerText = `${this.props.onScreenText}`
            }    
        });
    }

    
    
    render(){
    const {key, audio, onScreenText, id, activeStyle, style, muted, volume, trigger, setTrigger} = this.props

        return(
            <div>
                <div onClick={this.playSound} style={this.state.padActive === true ? activeStyle : style} className="Pad">
                        {id} 
                        {/* <br /> onScreenText == '#' ? null : onScreenText} */}
                    <audio>
                        <source src={audio} />    
                    </audio> 
                </div>
            </div>
        )
    }
}

export default NewPad