import React, {useState, useEffect} from 'react'

/* Import Components */
import { refreshPage } from './functions/refreshPage.js'
import NewPad from './components/NewPad.js'
/* Import Components */

/* Sound Imports */
import { nullData } from './Sound_Database/null' // If non of sound banks is selected
import { lofi } from './Sound_Database/lofi.js'
import { house } from './Sound_Database/house.js'
import { metal } from './Sound_Database/metal.js'
/* Sound Imports */

/* Import Styles */
import './styles/soundBankChange.css'
import './styles/refresh-button.css'
import './styles/volumeIcon.css'
/* Import Styles */

function App() {
  const [soundBank, setSoundBank] = useState(nullData)

  const [trigger, setTrigger] = useState(0)

  const [volume, setVolume] = useState(100)
  const [mute, setMute] = useState(false) // for Mute button

  /* Inactive And Active Styles For soundBankChange class */
  const [lofiBool, setLofiBool] = useState(false)
  const [houseBool, setHouseBool] = useState(false)
  const [metalBool, setMetalBool] = useState(false)

  const soundBankChangeActiveStyle = lofiBool === true 
                                   ? {fontSize: '30px', color: 'cyan'}
                                   : houseBool === true 
                                   ? {fontSize: '30px', color: 'red'}
                                   : metalBool === true
                                   ? {fontSize: '30px', color: 'black'}
                                   : null
  /* Inactive And Active Styles For soundBankChange class */

  // useEffect(() => { // For m Key (mute)
  //   document.addEventListener('keydown', e => {
  //     if(e.key === 'm'){
  //       setMute(!mute)
  //       mute === true ? setVolume(100) : setVolume(0)   
  //     }
  //   })
  // },[])

  useEffect(() => {
    console.log('Triggered')
    setMute(true)

      return () => {
        setTimeout(() => {
          setMute(false)
        }, 500) // 400ms is ganvmavlobashi muzika ar ukravs
      }
  }, [trigger])

  return (
    <div className="App">
      {/* Display Of What Is Playing */}
      <div id="display">
        <h5>Display</h5>
      </div>
      {/* Display Of What Is Playing */}
      
      {/* <div id="volume">
          <i 
            onClick={() => {
              setMute(!mute)
              mute === true ? setVolume(100) : setVolume(0)
            }}
            style={
              lofiBool === true 
              ? {fontSize:'25px', color: 'cyan'}
              : houseBool === true 
              ? {fontSize:'25px', color: 'red'}
              : metalBool === true
              ? {fontSize:'25px', color: 'black'}
              : {fontSize: '25px'}  }
            className={
            (volume > 0) ? 'volumeIcon fas fa-volume-up' : 
            (volume === 0) ? 'volumeIcon fas fa-volume-mute' : null
          } />
      </div> */}

      {/* soundBank Change */}
      <div className="soundBankChange">
        <ul>
          <li onClick={() => {
                setTimeout(() => {
                  setTrigger('trigger')
                }, 1000)
                setTrigger('nonTrigger')

            setSoundBank(lofi) // Simply changing soundBank value 

            let clicked = 0;
            clicked++;

            setLofiBool(clicked == 0 ? !lofiBool : true) // Tracking Witch Sound Bank Is Selected (For Dynamic Style) 
            
            setHouseBool(false) // Tracking Witch Sound Bank Is Selected (For Dynamic Style)
            setMetalBool(false) // Tracking Witch Sound Bank Is Selected (For Dynamic Style)
          }}
              style={lofiBool === false ? null : soundBankChangeActiveStyle}
          >Hip Hop</li>
          <li 
              onClick={() => {
                setTimeout(() => {
                  setTrigger('trigger')
                }, 1000)
                setTrigger('nonTrigger')

                setSoundBank(house) // Simply changing soundBank value :D 

                let clicked = 0;
                clicked++;

                setHouseBool(
                  clicked == 0 ? !houseBool : true
                ) // Tracking Witch Sound Bank Is Selected (For Dynamic Style)

                setLofiBool(false) // Tracking Witch Sound Bank Is Selected (For Dynamic Style)
                setMetalBool(false) // Tracking Witch Sound Bank Is Selected (For Dynamic Style)
              }}
              style={houseBool === false ? null : soundBankChangeActiveStyle} 
          >House</li>
          <li 
              onClick={() => {
                setTimeout(() => {
                  setTrigger('trigger')
                }, 1000)
                setTrigger('nonTrigger')

                setSoundBank(metal) // Simply changing soundBank value :D 

                let clicked = 0;
                clicked++;

                setMetalBool(
                  clicked == 0 ? !metalBool : true
                ) // Tracking Witch Sound Bank Is Selected (For Dynamic Style)

                setHouseBool(false) // Tracking Witch Sound Bank Is Selected (For Dynamic Style)
                setLofiBool(false) // Tracking Witch Sound Bank Is Selected (For Dynamic Style)
              }}
              style={metalBool === false ? null : soundBankChangeActiveStyle}
          >Metal</li>
        </ul>
      </div>
      {/* soundBank Change */}

      <div className="container">
        {/* Creating New Pads */}
        {soundBank.map((sound, index) => (
          <NewPad
            trigger={trigger}
            setTrigger={setTrigger}
            activeStyle={
              lofiBool === true 
            ? { color: 'cyan', border: '1px solid rgba(0, 255, 255, 0.253)', boxShadow: '1.7px 1.7px magenta', opacity: 1, fontSize:'23px'}
            : metalBool === true
            ? { color: 'red', backgroundColor: 'black', boxShadow: '1.5px 1.5px rgba(255, 0, 0, 0.7)', opacity: 1, fontSize:'23px'}
            : houseBool === true 
            ? {color: 'white', backgroundColor: 'gray', border: '1px solid white', boxShadow: '1.5px 1.5px white', opacity: 0.8}
            : null
            }
            style={
                lofiBool === true 
              ? { color: 'cyan', border: '1px solid rgba(0, 255, 255, 0.253)', boxShadow: '1.5px 1.5px white', opacity: 0.8}
              : houseBool === true 
              ? {}
              : metalBool === true
              ? { color: 'red', backgroundColor: 'black', boxShadow: '2px 2px rgba(255, 0, 0, 0.287)', opacity: 0.8}
              : null
            }
            muted={mute}
            key={index}
            audio={sound.src}
            onScreenText={sound.name}
            id={sound.keyTrigger}
          />       
        ))} 
        {/* Creating New Pads */}
      </div>

      <button style={
        lofiBool === true ? {color: 'cyan'}
      : houseBool === true ? {color: 'red'}
      : metalBool === true ? {color: 'black'} : null
      } className="refresh-button" onClick={refreshPage}><i className="fa fa-refresh"/> Refresh Machine</button>
    </div>
  );
}

export default App;
